/*eslint-disable */
export default function translate(text) {
  const rus = new RegExp(
    /^[]+$/
  );
  const eng = new RegExp(/^[a-zA-Z]+$/);
  const numbers = new RegExp(/^[0-9]+$/);
  const splitedText = text.split("");
  let newText = [];

  for (var i = 0; splitedText.length > i; i++) {
    if (rus.test(splitedText[i])) {
      if (splitedText[i] === "") {
        newText.push("a");
      }

      if (splitedText[i] === "") {
        newText.push("b");
      }
      if (splitedText[i] === "") {
        newText.push("v");
      }
      if (splitedText[i] === "") {
        newText.push("g");
      }
      if (splitedText[i] === "") {
        newText.push("d");
      }
      if (splitedText[i] === "") {
        newText.push("e");
      }
      if (splitedText[i] === "") {
        newText.push("e");
      }
      if (splitedText[i] === "") {
        newText.push("zh");
      }
      if (splitedText[i] === "") {
        newText.push("z");
      }
      if (splitedText[i] === "") {
        newText.push("i");
      }
      if (splitedText[i] === "") {
        newText.push("i");
      }
      if (splitedText[i] === "") {
        newText.push("k");
      }
      if (splitedText[i] === "") {
        newText.push("l");
      }
      if (splitedText[i] === "") {
        newText.push("m");
      }
      if (splitedText[i] === "") {
        newText.push("n");
      }
      if (splitedText[i] === "") {
        newText.push("o");
      }
      if (splitedText[i] === "") {
        newText.push("p");
      }
      if (splitedText[i] === "") {
        newText.push("r");
      }
      if (splitedText[i] === "") {
        newText.push("s");
      }
      if (splitedText[i] === "") {
        newText.push("t");
      }
      if (splitedText[i] === "") {
        newText.push("u");
      }
      if (splitedText[i] === "") {
        newText.push("f");
      }
      if (splitedText[i] === "") {
        newText.push("kh");
      }
      if (splitedText[i] === "") {
        newText.push("ts");
      }
      if (splitedText[i] === "") {
        newText.push("ch");
      }
      if (splitedText[i] === "") {
        newText.push("sh");
      }
      if (splitedText[i] === "") {
        newText.push("shch");
      }
      if (splitedText[i] === "") {
        newText.push("y");
      }
      if (splitedText[i] === "") {
        newText.push("e");
      }
      if (splitedText[i] === "") {
        newText.push("iu");
      }
      if (splitedText[i] === "") {
        newText.push("ia");
      }
    } else if (eng.test(splitedText[i])) {
      newText.push(splitedText[i]);
    } else if (splitedText[i] === " " || splitedText[i] === "_") {
      newText.push("_");
    } else if (numbers.test(splitedText[i])) {
      newText.push(splitedText[i]);
    }
  }

  let textToReturn = newText.join("").toLowerCase();
  return textToReturn;
}
